function eventListeners() {
    const mobileMenu = document.querySelector('.mobile-menu');

    mobileMenu.addEventListener('click', navegacionResponsive);

    // Muestra Campos Condicionales
    const metodoContacto = document.querySelectorAll('input[name="contacto[contactar]"]');
    metodoContacto.forEach(input => input.addEventListener('click', mostrarMetodosContacto))

    const motivoContacto = document.querySelectorAll('select[name="contacto[opciones]"]');
    motivoContacto.forEach(option => option.addEventListener('change', mostrarMotivoContacto))

}

function navegacionResponsive(){
    const navegacion = document.querySelector('.navegacion');
    const header = document.querySelector('.header');
    
    if(navegacion.classList.contains('mostrar') & header.classList.contains('crecer')){
        navegacion.classList.remove('mostrar') & header.classList.remove('crecer');
    } else{
        navegacion.classList.add('mostrar') & header.classList.add('crecer');
    }
}

function mostrarMetodosContacto(e) {
    const contactoDiv = document.querySelector('#contacto');
    if(e.target.value ===  'Telefono') {
        contactoDiv.innerHTML = `
        <p></p>
        <label for="telefono"></label>
        <input type="tel" placeholder="Escribe tu número de teléfono acá" id="telefono" name="contacto[telefono]">
        <br>
        <p class="noMTop">Elija la fecha y la hora para ser contactado</p>
        <label for="fecha">Fecha</label>
        <input type="date" id="fecha" name="contacto[fecha]">

        <label for="hora">Hora</label>
        <input type="time" id="hora" min="09:00" max="18:00" name="contacto[hora]">`;

    } else {
        contactoDiv.innerHTML = `
        <p></p>
        <label for="email"></label>
        <input type="mail" placeholder="Escribe tu email acá" id="email" name="contacto[email]" required>`;
    }
}

function mostrarMotivoContacto(e) {
    console.log(e);
    const motivoContacto = document.querySelector('#motivoContacto');
    if(e.target.value ===  'Cotizacion') {
        motivoContacto.innerHTML = `
        <label for="Cotizacion">Cotización:</label>
        <textarea id="Cotizacion" name="contacto[Cotizacion]" placeholder="Escribe acá lo que necesitas para poder ayudarte con una cotización"></textarea>
        `;

    } else if (e.target.value ===  'FAQ'){
        motivoContacto.innerHTML = `
        <label for="FAQ">Consulta:</label>
        <textarea id="FAQ" name="contacto[FAQ]" placeholder="Dejanos acá tus consultas"></textarea>
        `;
    } else {
        motivoContacto.innerHTML = `
        <label for="Otro">Mensaje:</label>
        <textarea id="Otro" name="contacto[Otro]" placeholder="Escribe acá lo que necesitas para poder ayudarte"></textarea>
        `;
    }
}


